import { environment } from "../environments/environment.js";

class SvgLoader {
    constructor() {}

    /**
     * Loads SVGs from the given set of icons in parallel.
     * @param {Map<string[]>} selectedIcons - A set of icons to load, where each item is an array containing the icon name and the color.
     * @param {number} concurrency - The number of threads to use when loading the SVGs. Defaults to 100.
     * @return {Promise<Array<string>>} - A promise that resolves to an array of SVGs, where each item is the loaded SVG as a string.
     */
    static loadSvgs(selectedIcons, concurrency = 100) {
        let numOfImages = selectedIcons.size;
        if (numOfImages < concurrency) concurrency = numOfImages;

        const results = new Array(numOfImages);
        let arrayOfIcons = [...selectedIcons];

        const threadPromises = [...Array(concurrency)].map(async () => {
            while (numOfImages) {
                const index = --numOfImages;
                const item = arrayOfIcons[index];
                results[index] = await this.#loadSvg(item[1], item[0]);
            }
        });

        return Promise.all(threadPromises).then(() => results);
    }

    /**
     * Loads the SVG string for the given icon from the given section.
     * @param {string} section - The section to load the SVG from.
     * @param {string} name - The name of the icon to load the SVG for.
     */
    static #loadSvg(section, name) {
        return new Promise((resolve, reject) => {
            const ajax = new XMLHttpRequest();
            ajax.open(
                "GET",
                environment.faSvgPath + section + "/" + name + ".svg",
                true
            );
            ajax.send();
            ajax.onload = function (e) {
                resolve(ajax.responseText);
            };
            ajax.onerror = function (e) {
                reject(e);
            };
        });
    }
}

export { SvgLoader };
